class ClassCom extends Component {
	constructor() {
		super()

		this.state = {
			num: 100
		}

		this.handleReduce = () => {
			console.log(this)
			this.setState({
				num: this.state.num - 1
			})
		}

		this.handleAdd = () => {
			this.setState(prevState => {
				console.log(prevState)
				return {
					num: prevState.num + 1
				}
			}, (currentState) => {
				console.log(currentState.num)
			})
		}
	}


	componentWillReceiveProps() {
		console.log('componentWillReceiveProps')

	}

	shouldComponentUpdate() {
		console.log('shouldComponentUpdate')
		return true
	}

	componentWillUpdate() {
		console.log('componentWillUpdate')

	}

	componentDidUpdate() {
		console.log('componentDidUpdate')

	}

	componentDidMount() {
		console.log('componentDidMount')
		for (let i = 0; i < 100; i ++) {
			this.setState(prevState => {
				// console.log('ä¸Šä¸€æ¬¡ state ä¸­ number' + prevState.num)
				return {
					num: prevState.num + 1
				}
			}, () => {
				// console.log(this.state.num)
			})
		}

	}

	componentWillMount() {
		console.log('componentWillMount')

	}


	render() {
		let { name } = this.props
		let { num } = this.state
		return (
			<div>
				<h1>hello { name }  </h1>
				<div className="calcWra">
					<button className="reduce" onClick={ this.handleReduce }>-</button>
					<span>{ num }</span>
					<button className="add" onClick={ this.handleAdd }>+</button>
				</div>

			</div>

		)
	}
}

