export default function (dom, key, value) {
	// if (key === 'className') {
	// 	key = 'class'
	// }

	if (key === 'style') {
		// <div style={{ color: 'red', fontSize: '14px' }}></div>
		if (isType(value) === 'Object') {
		// å¦‚æœæ˜¯å¯¹è±¡å½¢ä¼¼æˆ‘ä»¬éå†è®¾ç½®æ ·å¼
			for (let key in value) {
				dom.style[key] = value
			}

		// å¦‚æœæ˜¯æ™®é€šçš„ æˆ‘ä»¬ç›´æ¥è®¾ç½®è¡Œç±»æ ·å¼
		} else {
			dom.style.cssText = value
		}
	// å¦‚æœæ˜¯äº‹ä»¶
	} else if (/^on\w+/g.test(key)) {
		key = key.toLowerCase()
		// åªéœ€è¦æ·»åŠ äº‹ä»¶ onClick ==> onclick æˆ‘ä»¬ç»‘å®šæ—¶é—´ // TODO
		dom[key] = value || ''

	// å…¶ä»–çš„æˆ‘ä»¬ç›´æ¥èµ‹å€¼ç»™dom å°±è¡Œ
	} else {
		// å…¶ä»–çš„æˆ‘ä»¬æ‰‹åŠ¨è®¾ç½®
		dom[key] = value
	}
}